<template lang="html">
  <div class="userBaseInfo">
    <custHeader :title="title"></custHeader>
      <div class="list">
        <div class="" @click="headPic">
          <mt-cell title="头像"  style="height:70px;margin-bottom: 10px;  padding-top: 10px;">
            <img src=""  alt="" width="40px" height="40px" style="margin-top: 10px;margin-right: 10px;">
          </mt-cell>
        </div>
        <div class="" @click="urgentLinkClick">
            <mt-cell title="紧急联系人" >
              {{ this.urgentLink }}
            </mt-cell>
        </div>
        <div class="" @click="linkTelClick">
          <mt-cell title="联系电话" >
            {{ this.linkTel }}
          </mt-cell>
        </div>
        <div class="">
          <mt-cell title="身份认证" ></mt-cell>
        </div>
        <div class="">
          <mt-cell title="风险测评" is-link to="/riskAnalysis" style="margin-bottom:6px;"></mt-cell>
        </div>
        <div class="">
          <mt-cell title="修改登录密码" is-link></mt-cell>
        </div>


    </div>


    <mt-actionsheet
      :actions="headPicActions"
      v-model="headPicActionsVis"
    ></mt-actionsheet>


  </div>
</template>

<script>
import custHeader from  'components/custHeader'
import {MessageBox,Cell,Actionsheet} from 'mint-ui'
Vue.component(Cell.name,Cell);
Vue.component(Actionsheet.name,Actionsheet);
export default {
  data(){
    return {
      title:"个人基本信息",
      headPicActions:[{"name":"拍照","method":function(){}},{"name":"相册","method":function(){}}],
      headPicActionsVis:false,
      urgentLink:'',
      linkTel:''
    }
  },
  created:{
    // axiosUtil.
  },
  methods:{
    headPic:function(){
      this.headPicActionsVis = true
    },
    urgentLinkClick:function(){
      MessageBox.prompt("请输入紧急联系人").then(({value,action})=>{
        this.urgentLink = value
      })
    },
    linkTelClick:function(){
      MessageBox.prompt("请输入联系电话").then(({value,action})=>{
        this.linkTel = value
      })
    }
  },
 components:{
    custHeader
  },
}
</script>

<style lang="css">
.userBaseInfo{
    z-index: 5;
    position: fixed;
    width: 100%;
    top: 0;
}

.userInfoBody{
  overflow: scroll;
  height: 165vmin;
}
.userDiv{
  height: 60px;
  background-color: #fff;
  margin-bottom: 9px;
  line-height: 60px;
}
.userImg{
  float: left;
  width: 50px;
  height: 50px;
  margin-left: 20px;
  margin-top: 5px;
  margin-right: 40%;
}
</style>



